import * as React from "react";
import List from "@mui/material/List";
import ListItemButton from "@mui/material/ListItemButton";
import ListItemIcon from "@mui/material/ListItemIcon";
import DownloadIcon from "@mui/icons-material/Download";
import UploadIcon from "@mui/icons-material/Upload";
import ListItemText from "@mui/material/ListItemText";
import Collapse from "@mui/material/Collapse";
import Typography from "@mui/material/Typography";
import Fab from "@mui/material/Fab";
import { HelpTip } from "./HelpTip";
import { FileManager, FolderItem } from "../processor/FileSystem";
import { Divider, ListItem, ListSubheader } from "@mui/material";
import FolderIcon from "@mui/icons-material/Folder";
import { ExpandMore } from "@mui/icons-material";
import { DirectoryIcons } from "./DirectoryIcon";
import { UploadAlert } from "./FileOperationDialogs";
import { strip } from "../utils";

function NestedListContent(
  fileManager: FileManager,
  directory: FolderItem,
  update: React.Dispatch<React.SetStateAction<FileManager>>,
  lv: number
): JSX.Element[] {
  const onDirSelected: (
    event: React.MouseEvent<HTMLDivElement, MouseEvent>,
    dirId: number
  ) => void = (event, dirId) => {
    fileManager.enterDir(dirId);
    update(fileManager.getCopy());
  };

  return directory.content.map((value) => {
    if (value.type === "File")
      return (
        <ListItem key={value.id}>
          <ListItemButton onClick={(event) => onDirSelected(event, value.id)}>
            <ListItemIcon>
              {DirectoryIcons(value.name, value.type)}
            </ListItemIcon>
            <ListItemText primary={value.name} />
          </ListItemButton>
        </ListItem>
      );
    else
      return (
        <>
          <ListItem key={value.id}>
            <ListItemButton onClick={(event) => onDirSelected(event, value.id)}>
              <ListItemIcon>
                <FolderIcon />
              </ListItemIcon>
              <ListItemText primary={value.name} />
              {/* <ExpandMore /> */}
            </ListItemButton>
          </ListItem>
          <Collapse in={true} sx={{ pl: lv }}>
            <List dense>
              {NestedListContent(
                fileManager,
                value as FolderItem,
                update,
                lv + 2
              )}
            </List>
          </Collapse>
        </>
      );
  });
}

function NestedList(
  fileManager: FileManager,
  setFileManager: React.Dispatch<React.SetStateAction<FileManager>>
): JSX.Element {
  const onRootSelected: (
    event: React.MouseEvent<HTMLDivElement, MouseEvent>,
    dirId: number
  ) => void = (event, dirId) => {
    fileManager.enterDir(dirId);
    setFileManager(fileManager.getCopy());
  };

  const downloadFileSystemJson = (fileManager: FileManager) => {
    var jsonString = JSON.stringify(fileManager.toJsonString());
    jsonString = jsonString.replaceAll(/\\(?![ntrvb])/g, "");
    jsonString = strip(jsonString, '"');
    const dataStr: string =
      "data:application/json;charset=utf-8," + encodeURIComponent(jsonString);
    const download: HTMLAnchorElement = document.createElement("a");
    download.setAttribute("href", dataStr);
    var d = new Date();
    download.setAttribute(
      "download",
      "FileRecord" +
        `${d.getFullYear()}${
          d.getMonth() + 1
        }${d.getDate()}_${d.getHours()}${d.getMinutes()}${d.getSeconds()}` +
        ".json"
    );
    document.body.appendChild(download);
    download.click();
    download.remove();
  };

  const [uploaderOpen, setUploaderOpen] = React.useState(false);

  return (
    <List sx={{ overflow: "auto" }} dense>
      <ListSubheader>
        <Typography variant="body1" align="center" color={"primary"}>
          {"目录选择视图"}
        </Typography>
        <Divider sx={{ mb: 1, mt: 1 }} />
        <Typography variant="body1" align="center" color={"primary"}>
          <HelpTip title={"以 json 格式保存当前文件系统记录到本地"}>
            <Fab
              variant="extended"
              color="primary"
              size="small"
              onClick={(e) => downloadFileSystemJson(fileManager)}
            >
              <DownloadIcon />
              {"保存目录"}
            </Fab>
          </HelpTip>
          <HelpTip title={"上传本地保存的文件系统 json 记录"}>
            <Fab
              variant="extended"
              color="primary"
              size="small"
              sx={{ ml: 2 }}
              onClick={() => setUploaderOpen(true)}
            >
              <UploadIcon />
              上传目录
            </Fab>
          </HelpTip>
          {UploadAlert(
            uploaderOpen,
            setUploaderOpen,
            fileManager,
            setFileManager
          )}
        </Typography>
        <Divider sx={{ mt: 2 }} />
      </ListSubheader>
      <ListItem key={0}>
        <ListItemButton onClick={(event) => onRootSelected(event, 0)}>
          <ListItemIcon>{DirectoryIcons("root", "Folder")}</ListItemIcon>
          <ListItemText primary={"root"} />
          <ExpandMore />
        </ListItemButton>
      </ListItem>
      <Collapse in={true}>
        <List dense sx={{ pl: 2 }}>
          {NestedListContent(
            fileManager,
            fileManager.getState(),
            setFileManager,
            2
          )}
        </List>
      </Collapse>
    </List>
  );
}

export { NestedList };
