import * as React from "react";
import Button from "@mui/material/Button";
import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import DialogContentText from "@mui/material/DialogContentText";
import DialogTitle from "@mui/material/DialogTitle";
import TextField from "@mui/material/TextField";
import { useDropzone } from "react-dropzone";
import { DirItemType, FileManager } from "../processor/FileSystem";
import { strip } from "../utils";

// 删除文件时的弹窗
function FolderDeletionAlert(
  open: boolean,
  handleCancel: () => void,
  handleConfirm: () => void,
  title: string,
  content: string
): JSX.Element {
  return (
    <div>
      <Dialog open={open} onClose={handleCancel} maxWidth="md" fullWidth>
        <DialogTitle id="alert-dialog-title">{title}</DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
            {content}
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleCancel}>再想想</Button>
          <Button onClick={handleConfirm} autoFocus>
            我知道我在做什么
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}

// 重命名文件时的弹窗
function ItemRenameAlert(
  open: boolean,
  handleCancel: () => void,
  handleConfirm: (newName: string) => void
): JSX.Element {
  const [newName, setNewName] = React.useState("");
  const onTextInput = (content: string) => {
    setNewName(content);
  };

  return (
    <div>
      <Dialog open={open} onClose={handleCancel} maxWidth="md" fullWidth>
        <DialogTitle id="alert-dialog-title">{"重命名文件或目录"}</DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
            {"重命名文件或目录"}
          </DialogContentText>
          <TextField
            autoFocus
            margin="dense"
            id="name"
            label="新目录名"
            type="email"
            fullWidth
            variant="standard"
            onChange={(e) => onTextInput(e.target.value)}
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={handleCancel}>取消</Button>
          <Button onClick={(e) => handleConfirm(newName)} autoFocus>
            确定
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}

// 新建目录时的弹窗
function NewDirAddAlert(
  open: boolean,
  handleCancel: () => void,
  handleConfirm: (name: string) => void,
  type: DirItemType
): JSX.Element {
  const [name, setName] = React.useState("");
  const onTextInput = (content: string) => {
    setName(content);
  };

  return (
    <>
      <Dialog open={open} onClose={handleCancel} maxWidth="md" fullWidth>
        <DialogTitle id="alert-dialog-title">
          {type === "File" ? "新建文件" : "新建目录"}
        </DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
            {`输入${type === "File" ? "文件" : "目录"}名`}
          </DialogContentText>
          <TextField
            autoFocus
            margin="dense"
            id="name"
            label={type === "File" ? "文件名" : "目录名"}
            type="email"
            fullWidth
            variant="standard"
            onChange={(e) => onTextInput(e.target.value)}
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={handleCancel}>取消</Button>
          <Button onClick={(e) => handleConfirm(name)} autoFocus>
            确定
          </Button>
        </DialogActions>
      </Dialog>
    </>
  );
}

// 上传目录时的弹窗
function UploadAlert(
  alertOpen: boolean,
  setAlertOpen: React.Dispatch<React.SetStateAction<boolean>>,
  fileManager: FileManager,
  setFileManager: React.Dispatch<React.SetStateAction<FileManager>>
): JSX.Element {
  const { getRootProps, getInputProps, open, acceptedFiles } = useDropzone({
    // Disable click and keydown behavior
    noClick: true,
    noKeyboard: true,
    maxFiles: 1,
  });

  const files = acceptedFiles.map((file) => (
    <li key={file.name}>
      {file.name} - {file.size} bytes
    </li>
  ));
  const handleCancel = () => setAlertOpen(false);

  const handleConfirm = () => {
    if (acceptedFiles.length <= 0)
      return;
    const handleReadFile = (content: string) => {
      const newManager = fileManager.fromJsonString(content);
      setFileManager(newManager.getCopy());
    }
    acceptedFiles[0].text().then((content: string) => handleReadFile(strip(content, '"')));
    setAlertOpen(false);
  };

  return (
    <>
      <Dialog open={alertOpen} onClose={handleCancel} maxWidth="md" fullWidth>
        <DialogTitle id="alert-dialog-title">{"上传文件系统记录"}</DialogTitle>
        <DialogContent>
          <div className="container">
            <div {...getRootProps({ className: "dropzone" })}>
              <input {...getInputProps()} />
              <p>在此处选择文件系统记录的json文件</p>
              <button type="button" onClick={open}>
                选择文件
              </button>
            </div>
            <aside>
              <h4>Files</h4>
              <ul>{files}</ul>
            </aside>
          </div>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleCancel}>取消</Button>
          <Button onClick={(e) => handleConfirm()} autoFocus>
            确定
          </Button>
        </DialogActions>
      </Dialog>
    </>
  );
}

export { FolderDeletionAlert, ItemRenameAlert, NewDirAddAlert, UploadAlert };
